import { Component, OnInit } from '@angular/core';
import { AuthenticationService } from '../services/auth.service';
import { ToastrService } from 'ngx-toastr';
import { Router } from '@angular/router';

@Component({
  selector: 'app-login',
  templateUrl: './login.component.html',
  styleUrls: ['./login.component.css']
})
export class LoginComponent implements OnInit {
  images: any[] =[];
  account:any={
    UserName: '',
    Password: '',
    RememberMe: true,
  };
  error:number;
  emes:any='';
  loginState = true;
  seePass:boolean=false;
  responsiveOptions:any[] = [
      {
          breakpoint: '1024px',
          numVisible: 5
      },
      {
          breakpoint: '768px',
          numVisible: 3
      },
      {
          breakpoint: '560px',
          numVisible: 1
      }
  ];

  constructor(private _auth:AuthenticationService,private toastr:ToastrService,private _router:Router) { }

  ngOnInit() {
    for (let i = 1; i <=5; i++) {
      this.images.push(`assets/logo/${i}.png`)
    }
  }
  timMK(){
    console.log('asdasda')
  }
  validate() {
    if (this.account.UserName === undefined || this.account.UserName.trim() === '') {
      this.error = 1;
      this.emes = 'Vui lng in tn ng nhp!';
      return false;
    } else if (this.account.Password === undefined || this.account.Password.trim() === '') {
      this.error = 2;
      this.emes = 'Vui lng nhp mt khu!';
      return false;
    }
    // this.toastr.error(this.emes);
    return true;
  }
  login(){
    if (this.validate()) {
      this._auth.login(this.account).subscribe((res: any) => {
        console.log(res);
        if (res) {
          if (res.Error === 4) {
            this.loginState = true;
            this.error = 0;
            this.toastr.success('ng nhp thnh cng!');
            this._router.navigate(['/quantri'])
          } else {
            this.loginState = false;
            this.error = 0;
            this.toastr.error('ng nhp tht bi!');
          }
        } else {
          this.loginState = false;
          this.error = 0;
          this.toastr.error('ng nhp tht bi!');
        }
      });
    } else {
      this.toastr.error(this.emes);
    }
  }
  revealPass(){
    this.seePass = !this.seePass;
  }
}
