import { Component, OnInit } from '@angular/core';
import { NgbModal } from '@ng-bootstrap/ng-bootstrap';
import { ToastrService } from 'ngx-toastr';
import { Dat09Service } from 'src/app/services/callApi';
import { ModalquytrinhsapxepComponent } from '../modal/modalquytrinhsapxep/modalquytrinhsapxep.component';

@Component({
  selector: 'app-quytrinhsapxep',
  templateUrl: './quytrinhsapxep.component.html',
  styleUrls: ['./quytrinhsapxep.component.css']
})
export class QuytrinhsapxepComponent implements OnInit {
  items: any = [];
  filter:any={};
  listLoaiPhuongAn:any=[];
  trangThai:any=1;
  paging: any = { CurrentPage: 1, TotalPage: 1, TotalItem: 100 };
  cols: any = [
    {
      header: 'n v',
      field: 'DonVi',
      width: '150px'
    },
    {
      header: 'Ngy khi to',
      field: 'NgayKhoiTao',
      width: '150px'
    },
    {
      header: 'S quy trnh',
      field: 'SoQuyTrinh',
      width: '150px'
    },
    {
      header: 'Ni dung',
      field: 'NoiDung',
      width: '200px'
    },
    // {
    //   header: 'Din tch',
    //   field: 'DienTich',
    //   width: '150px'
    // },
    // {
    //   header: 'Hin trng s dng',
    //   field: 'HienTrangSuDung',
    //   width: '400px'
    // },
    // {
    //   header: 'Phng n  xut',
    //   field: 'PhuongAnDeXuat',
    //   width: '150px'
    // },
    {
      header: 'Trng thi',
      field: 'TenTrangThai',
      width: '150px'
    }
  ];

  colsQuyTrinh: any = [
    {
      header: 'Ngy nhn',
      field: 'NgayKhoiTao',
      width: '150px'
    },
    {
      header: 'Ngy chuyn',
      field: 'SoQuyTrinh',
      width: '150px'
    },
    {
      header: 'Thi gian x l',
      field: 'DiaChi',
      width: '200px'
    },
    {
      header: 'B phn x l',
      field: 'DienTich',
      width: '150px'
    },
    {
      header: 'Ni dung x l',
      field: 'HienTrangSuDung',
      width: '400px'
    },
  ];


  constructor(public _modal:NgbModal,public _toastr:ToastrService,private _service:Dat09Service) { }

  ngOnInit(): void {
    this.GetListQuyTrinh()
  }
  add(){
    let modalRef = this._modal.open(ModalquytrinhsapxepComponent, {
      size: 'fullscreen',
      backdrop: 'static'
    })
    modalRef.componentInstance.opt = 'add';
    modalRef.componentInstance.item = {
      ID:null,
      TepDinhKems:[],
      templistTaiSanQuyTrinh:[],
      listTaiSanQuyTrinh:[]
    }
    modalRef.result.then((res: any) => {
      console.log(res);
      this._toastr.success('Cp nht thnh cng');
      this.GetListQuyTrinh();
    })
      .catch(er => { console.log(er) })
  }
  update(item){
    let modalRef = this._modal.open(ModalquytrinhsapxepComponent, {
      size: 'fullscreen',
      backdrop: 'static'
    })
    modalRef.componentInstance.opt = 'edit';
    modalRef.componentInstance.item = JSON.parse(JSON.stringify(item));
    modalRef.result.then((res: any) => {
      console.log(res);
      this._toastr.success('Cp nht thnh cng');
      this.GetListQuyTrinh();
    })
      .catch(er => { console.log(er) })
  }
  changeTab(e){
    this.trangThai = e.index+1;
    this.GetListQuyTrinh();
  }
  GetListQuyTrinh(){
    let data={
      PageSize: 5,
      CurrentPage: this.paging.CurrentPage,
      TabTrangThai: this.trangThai,
      sFilter:'',
      Ma: "",
      Ten: "",
    }
    this._service.GetListQuyTrinh(data).subscribe((res:any)=>{
      this.items = res.items;
      this.paging = res.paging;
    })
  }
}
