import { Component, OnInit, Input, Output, EventEmitter, DoCheck } from '@angular/core';
import { NgbModal } from '@ng-bootstrap/ng-bootstrap';
import { ModalTaiSanTrenDatComponent } from '../../modal-tai-san-tren-dat/modal-tai-san-tren-dat.component';

@Component({
  selector: 'app-crud-tai-san-tren-dat',
  templateUrl: './crud-tai-san-tren-dat.component.html',
  styleUrls: ['./crud-tai-san-tren-dat.component.css']
})
export class CrudTaiSanTrenDatComponent implements OnInit, DoCheck {
  @Input('TaiSanDatNhas') items: any = [];
  @Output('TaiSanDatNhas') itemChange: EventEmitter<any> = new EventEmitter<any>();
  paging: any = { CurrentPage: 1, TotalPage: 1, TotalItem: 100 };
  cols: any = [
    {
      header: 'Loi ti sn',
      field: 'TenLoaiTaiSan',
      width: '200px',
      pipe: ''

    },
    {
      header: 'Tn ti sn',
      field: 'Ten',
      width: '200px',
      pipe: ''

    },
    {
      header: 'Din tch s dng',
      field: 'DienTichSan',
      width: '100px',
      pipe: 'number'
    },
    {
      header: 'Din tch t xy dng',
      field: 'DienTichXayDung',
      width: '100px',
      pipe: 'number'
    },

    {
      header: 'Cp/hng cng trnh',
      field: 'TendmCapHang',
      width: '100px',
      pipe: ''

    },
    {
      header: 'S tng',
      field: 'SoTang',
      width: '100px',
      pipe: ''

    },
    {
      header: 'Nm xy dng',
      field: 'NamXayDung',
      width: '100px',
      pipe: ''

    },
    {
      header: 'S nm  SD',
      field: 'SoNamSuDung',
      width: '100px',
      pipe: ''

    },
    {
      header: 'S nm cn li SD',
      field: 'SoNamSuDungConLai',
      width: '100px',
      pipe: ''

    },
    {
      header: 'Tui th',
      field: 'TuoiTho',
      width: '100px',
      pipe: ''

    },
    {
      header: 'Thi gian khu hao',
      field: 'ThoiGianKhauHao',
      width: '100px',
      pipe: ''
    },
    {
      header: 'Nguyn gi',
      field: 'NguyenGia',
      width: '100px', 
      pipe: 'number'
    },
    {
      header: 'Gi tr cn li',
      field: 'GiaTriConLai',
      width: '100px',
      pipe: 'number'
    },
  ];

  constructor(private _modal: NgbModal) { }

  ngOnInit(): void {
  }
  ngDoCheck(): void {
    this.itemChange.emit(this.items);
  }
  add() {
    let modalRef = this._modal.open(ModalTaiSanTrenDatComponent, { size: 'xl', backdrop: 'static' });
    modalRef.componentInstance.item = {
      ID: 0,
      TepDinhKems: [],
      BanVe: [],
      LoaiTaiSan: {}
    };
    modalRef.componentInstance.opt = 'add';
    modalRef.result.then(res => {
      console.log(res.item);
      this.items.push(res.item);
      if (res.opt !== 'add') {
        this.add()
      }
    }).catch(er => { console.log(er) });
  }
  edit(item, i) {
    let modalRef = this._modal.open(ModalTaiSanTrenDatComponent, { size: 'xl', backdrop: 'static' });
    modalRef.componentInstance.item = JSON.parse(JSON.stringify(item));
    modalRef.componentInstance.opt = 'edit';
    modalRef.result.then(res => {
      this.items.splice(i, 1);
      this.items.push(res.item);
      if (res.opt !== 'add') {
        this.add()
      }
    }).catch(er => { console.log(er) });
  }
  delete(i) {
    let item = this.items.splice(i, 1)[0];
    if (item.ID === 0) {
    } else {
      item.isXoa = true;
      this.items.push(JSON.parse(JSON.stringify(item)));
    }

    // this.items[i].isXoa = true;
    // this.items.push(this.items.splice(i,1));
    console.log(item);
    console.log(this.items);
  }
}
